.contact1{
    padding-bottom: 21%;
    width: 100%;
    .contact_content{
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 40px;
        .div1{
            flex: 1;
            display: flex;
            flex-direction: column;
                h3{
                    margin-bottom: 10px;
                    font-size: 2.5rem;
                    font-weight: 700;
                    color: #f5f5f5;
                }
                p {
                    color: #f5f5f5;
                    font-size: 1.8rem;
                    line-height: 1.65;
                    padding-bottom: 20px;
                    border-bottom: 1px dashed var(--yellow-theme-main-color);
                }
                ul > li{
                    line-height: 1rem;
                    margin-bottom: 15px;
                    list-style: none;
        
                    span.title, span.value {
                        font-size: 18px;
                        font-weight: 700;
                        letter-spacing: 0.5px;
                        border-bottom: 2px solid var(--yellow-theme-main-color);
                        position: relative;
                        display: inline-block;
                        margin-right: 16px;
                        padding-bottom: 1px;
        
                        color: #f5f5f5;
                    }
                    span.value {
                        border: none;
                    }
                }
                .personal-info {
                    margin-top: 20px;
                    margin-bottom: 20px;
        
                }
        }
        .div2{
            flex: 1;
        }
        .contact_content_header-text{
            color: #f5f5f5;
            flex: 1;
            display: inline-block;
            font-size: 22px;
            font-weight: 600;
            line-height: 1em;
            letter-spacing: 0.5px;
            position: relative;
            text-transform: uppercase;
            padding: 5px 10px;
            border-bottom: 2px solid #f5f5f5;
        }
        .contact_content_header-text::before,
        .contact_content_header-text::after{
            position: absolute;
            content: "";
            width: 2px;
            height: 5px;
            background-color: #f5f5f5;
            bottom: -1px;
        }
        .contact_content_header-text::before{
            left: 0;
        }
        .contact_content_header-text::after{
            right: 0;
        }
        .contact_content_form{
            margin-top: 30px;
            &_controlswrapper{
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                gap: 40px;
                margin-bottom: 30px;
                div {
                    position: relative;
                    .inputName, .inputEmail, .inputDescription{
                        width: 100%;
                        border: 2px solid #333333;
                        padding: 16px 12px;
                        transition: all 0.5s ease;
                        position: relative;
                        background-color: transparent;
                        color: #f5f5f5;
                    }
                    .nameLabel,.emailLabel,.descriptionLabel{
                        font-weight: 500;
                        position: absolute;
                        top: 7px;
                        left: 12px;
                        transition: all 0.2s ease;
                        color: #f5f5f5;
                        font-size: 16px;
                    }
                    .inputName:focus, .inputEmail:focus, .inputDescription:focus{
                        outline: none;
                    }
                    .inputName:focus + .nameLabel,.inputName:valid + .nameLabel, .inputEmail:focus + .emailLabel,.inputEmail:valid + .emailLabel, .inputDescription:focus + .descriptionLabel,.inputDescription:valid + .descriptionLabel{
                        font-size: 18px;
                        top: -25px;
                        left: 0px;
                    }

                }

            }
            button {
                cursor: pointer;
                padding: 10px 70px;
                font-size: 2rem;
                background: transparent;
                color: #f5f5f5;
                border: 1.5px solid #f5f5f5;
                border-radius: 4px;
                font-weight: 600;
                transition: 0.5s ease;
                &:hover{
                    color: #333;
                    background: #f5f5f5;
                }
            }

        }
        .handlesubmit{
            color: #f5f5f5;
            display: inline-block;
            font-size: 22px;
            font-weight: 600;
            line-height: 1em;
            letter-spacing: 0.5px;
            position: relative;
            text-transform: uppercase;
            padding: 5px 10px;
        }
    }
}
@media only screen and (max-width: 768px){
    .contact_content{
        flex-direction: column;
    }
    .contact_content_form{
        text-align: center;
        &_controlswrapper{
            grid-template-columns: repeat(1,1fr);
        }
        margin-bottom: 100px;
    }
}
