#HOMEE{

    height: 100vh;
    width: 100%;

.homee {
    position: absolute;
    color: #F5F5F5;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 100;
    &_text-wrapper {
        font-size: 8rem;
        width: 100%;
    }
    #tsparticles{
        width: 100%;
        height: 100%;
    }
    .homee_text-wrapper{
        h1 {
            margin: 0px;
            overflow: hidden;
            white-space: nowrap;
            animation: typingEffectAnimation 2.5s steps(30, end);
        }
    }

    button {
        cursor: pointer;
        padding: 10px 70px;
        font-size: 2rem;
        background: transparent;
        color: var(--yellow-theme-main-color);
        border: 1.5px solid var(--yellow-theme-main-color);
        border-radius: 4px;
        font-weight: 500;

        &:hover {
            background: var(--yellow-theme-main-color);
            color: #000000;
        }
    }
}
}
@media only screen and (max-width: 550px) {
    #HOMEE{
        .homee_text-wrapper {
            font-size: 3rem;
            line-height: 5rem;
        }
    }

}
@media only screen and (max-width: 390px) {
    #HOMEE{
        .homee_text-wrapper {
            font-size: 1.5rem;
            line-height: 4rem;
        }
    }

}
@media only screen and (min-width: 551px) and  (max-width: 768px) {
    #HOMEE{
        .homee_text-wrapper {
            font-size: 4rem;
            line-height: 6rem;
        }
    }
}
@media only screen and (min-width: 769px) and  (max-width: 990px) {
    #HOMEE{
        .homee_text-wrapper {
            font-size: 6rem;
            line-height: 8rem;
        }
    }

}
@keyframes typingEffectAnimation {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}