.App {
  text-align: center;
  height: 100%;
  display: block;
  background: #F5F5F5;
}
.App2 {
  text-align: center;
  height: 100%;
  display: block;
  background: #282c34;  
}
.loader{
  margin: 25% auto;
  width: 500px;
  height: 500px;
}

.App-logo {
  height: 100%;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
